
.beings-table-wrapper {
  margin-top: 1rem;
}

.table-title {
  font-family: 'Bangers', sans-serif;
  font-size: 2.5rem;
  color: #e60000;
  margin-bottom: 0.5rem;
  text-align: center;
}

.controls-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.75rem;
}

.rows-selector {
  font-size: 0.9rem;
  padding: 0.25rem 0.5rem;
  border: 1px solid #ccc;
}

.beings-table-container {
  padding: 1rem 2rem;
  background-color: #fdf6e3;
  font-family: Helvetica, sans-serif;
  color: #000;
  overflow-x: auto;
}

.pagination {
  display: flex;
  gap: 0.4rem;
  justify-content: flex-start; /* Move pagination to the left */
  margin-bottom: 0.75rem;
}

.pagination button {
  background-color: #e60000;
  color: white;
  border: none;
  padding: 0.35rem 0.65rem;
  font-weight: bold;
  border-radius: 3px;
  cursor: pointer;
  font-size: 0.9rem;
}

.pagination button.active {
  background-color: #000;
  color: #fff;
}

.beings-table {
  width: 100%;
  min-width: 1200px;
  border-collapse: collapse;
}

.beings-table th,
.beings-table td {
  padding: 0.4rem 0.5rem;
  text-align: left;
  border: 2px solid #000;
  white-space: nowrap;
  height: 32px; /* row height reduced by ~20% */
}

.beings-table th {
  background-color: #e60000;
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  border-bottom: 3px solid #000;
  font-size: 0.85rem; /* header font reduced by 15% */
}

.verbal {
  font-size: 0.8rem;
}

.numeric {
  font-size: 1.25rem; /* Power Rating font increased by 25% */
  font-weight: 500;
}

.sortable-header::after {
  content: " â–¼";
  font-size: 0.8rem;
  cursor: pointer;
  margin-left: 5px;
}

.beings-table tr:nth-child(even) {
  background-color: #fbe8e8;
}

.beings-table tr:nth-child(odd) {
  background-color: #ffffff;
}

.character-photo {
  width: 40px;
  height: 40px;
  object-fit: cover;
  border-radius: 4px;
}
